.ChallengeSection {
  height: 100%;
}
.ChallengeSpace {
  /* height: 100vh; */
  display: flex;
  justify-content: space-between;
  /* align-items: center; */
  margin: 0 auto;
  transition: 1s;
  padding: 180px 0;
}
/* .titleWrap{
  width: 100%;
} */
.more {
  position: relative;
  width: max-content;
  transition: 1s;
}
.moreBtn {
  position: absolute;
  width: 50px;
  height: 50px;
  top: 50%;
  transform: translate(0, -50%);
  border-radius: 25px;
  background: linear-gradient(135deg, #0a47fd 0%, #7b00df 100%);
  opacity: 0.7;
  z-index: -1;
  transition: 1s;
}
.moreText{
  margin: 10px;
}
.more > a {
  font-size: 18px;
  font-weight: 500;
  color: #000;
  transition: 0.5s;
}
.more:hover > a {
  color: #fff;
}
.more:hover .arrowHead {
  transform: translate(0);
  stroke: #fff;
}
.more:hover .arrowLine {
  stroke-dashoffset: 0px;
  stroke: #fff;
}
.more:hover > .moreBtn {
  background: linear-gradient(135deg, #0a47fd 0%, #7b00df 100%);
  opacity: 1;
  width: 110%;
}
@media(max-width:1024px){
  .ChallengeSpace{
    /* height: 100%; */
    flex-direction: column;
  }
  .ChallengeBox{
    /* margin: 30px 0; */
    margin: 0;
  }
  .ChallengeBox:nth-of-type(2){
    margin: 0 20px;
  }
}
@media (max-width:768px) {
  .ChallengeSpace{
    padding: 150px 0;
  }
}
@media (max-width:460px) {
  .ChallengeBox:nth-of-type(2){
    margin: 0;
  }
  .ChallengeBox{
    margin: 30px 0;
  }
}